.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fromTop {
  border-left: 6px solid black;
  height: 2vh;
  position: absolute;
  margin-left: 4%;
  z-index: -1;
}

.toBottom {
  border-left: 6px solid black;
  height: 9vh;
  position: absolute;
  margin-top: 1%;
  margin-left: 4%;
  z-index: -1;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent; /* make scrollbar transparent */
}